:root {
  --primary: #ff9000;
  --current: #cbe2b5;
  --highlight: #7fa1c3;
}

.highlight {
  color: #222;
  background-color: var(--current) !important;
}

.highlight + * {
  color: #fff;
  background-color: var(--highlight) !important;
}

body {
  color: #333;
  background-color: #f4f4f9;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

h1 {
  text-align: center;
  color: var(--primary);
  margin-top: 20px;
}

.container {
  background-color: #fff;
  border-radius: 10px;
  width: 80%;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

table {
  border-collapse: collapse;
  width: 100%;
  margin-top: 20px;
}

table thead {
  background-color: var(--primary);
  color: #fff;
}

table, th, td {
  border: 1px solid #ddd;
}

th, td {
  text-align: left;
  padding: 12px;
}

th {
  font-size: 16px;
}

tr:nth-child(2n) {
  background-color: #f9f9f9;
}

.shift-info {
  text-align: center;
  margin-top: 10px;
  font-size: 18px;
}

.color-info {
  position: absolute;
}

.box {
  border-radius: 4px;
  width: 20px;
  height: 20px;
}

.box.current {
  background-color: var(--current);
}

.box.upcoming {
  background-color: var(--highlight);
}
